import React, { useEffect } from "react";
import { Input, Segmented, Button, Checkbox, Form, Alert, Spin } from "antd";
import { useAppDispatch, useAppSelector } from "../../hooks/redux";
import { register } from "../../store/reducers/ActionCreators";
import classes from "./Form.module.css";

type propType = {
  handleCancel: () => void;
};

const RegisterFrom: React.FC = ({ handleCancel }: propType) => {
  const dispatch = useAppDispatch();
  const {
    register: data,
    isLoading,
    error,
  } = useAppSelector((state) => state.registerReducer);

  const onFinish = (values: any) => {
    console.log("Success:", values);
    dispatch(register(values));
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log("Failed:", errorInfo);
  };

  useEffect(() => {
    console.log("DATA", data);
    data && handleCancel();
  }, [data]);

  return (
    <div>
      <Form
        name="basic"
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        layout="vertical"
        autoComplete="off"
      >
        <Form.Item
          label=""
          name="username"
          rules={[{ required: true, message: " " }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="email"
          label=""
          rules={[
            {
              type: "email",
              message: "  ",
            },
            {
              required: true,
              message: " ",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="password"
          rules={[
            { required: true, message: " " },
            () => ({
              validator(_, value) {
                if (value.length >= 8) {
                  return Promise.resolve();
                }
                return Promise.reject(
                  new Error("     8 !")
                );
              },
            }),
          ]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          label=" "
          name="rePassword"
          dependencies={["password"]}
          hasFeedback
          rules={[
            {
              required: true,
              message: " ",
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue("password") === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error("  !"));
              },
            }),
          ]}
        >
          <Input.Password />
        </Form.Item>
        {isLoading && <Spin />}
        {!isLoading && error && <Alert message={error} type="error" showIcon />}
        <Form.Item>
          <div className={classes.footerForm}>
            <Button type="primary" htmlType="submit">
              
            </Button>
            <Button style={{ marginLeft: "8px" }} onClick={handleCancel}>
              
            </Button>
          </div>
        </Form.Item>
      </Form>
    </div>
  );
};

export default RegisterFrom;
